*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family:'Times New Roman', Times, serif
}
:root{
    --color-131921: #131921;
    --color-3b4149: #3b4149;
    --color-febd69: #febd69;
    --color-232f3e: #232f3e;
    --color-bf4800:#bf4800;
}
body{
    font-family: 'Rubik', sans-serif;
    background:#eaeaea;
}
a{
    text-decoration: none;
    display: inline-block;
}
.form-control:focus{
    box-shadow: none;
    border-color: inherit;
}

 /* header start */
 .header-top-strip{
    border-bottom: 1px solid var(--color-3b4149);
    background-color: var(--color-131921);
 }
 .header-upper{
    background-color: var(--color-131921);
 }
 .header-bottom{
    background-color: var(--color-232f3e);
 }
 .header-upper .input-group-text{
    background-color: var(--color-febd69);
 }
 .header-upper-links img{
    width: 35px;
    height: 35px;
 }
 .header-upper-links .ul-links{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    color: white;
 }
 .menu-links-li{
    display: flex;
    align-items: center;
    gap: 15px;
 }
 .header-bottom .menu-links a,.header-bottom .dropdown button{
    color: white;
    font-style: 14px;
    line-height: 18px;
    font-weight: 400;
    letter-spacing: 0.3;
    text-transform: uppercase;
 }
 .drop-btn{
    display: flex;
    align-items: center;
    gap: 10px;
 }
 .header-bottom .dropdown button:focus{
    box-shadow: none;
 }
 .header-bottom .dropdown-menu{
    background-color: var(--color-131921);
    width: 100%;
    transform: translate3d(0px,57px,0px) !important;
 }
 .header-bottom .dropdown-item{
    padding: 20px;
    margin-bottom: 3px;
    border: 1px solid var(--color-3b4149);
 }
 .header-bottom .dropdown-item:hover{
    background-color: transparent;
    color: var(--color-febd69) !important;
 }
 .menu-bottom{
    gap: 30px;
 }

  /* header end here */

 /* footer start here */

 footer{
    background-color: var(--color-232f3e);
 }
 footer:not(:first-child){
    border-top: 1px solid rgba(255, 255, 255, 0.1);
 }
 .footer-top-data{
    gap: 30px;
 }
 footer .input-group-text {
    background-color: var(--color-3b4149);
    color: white;
    padding: 7px 18px;
 }
 .social-icons{
    gap: 30px;
 }

 /* Home Page */
 .main-banner-content{
   top: 15%;
   left: 5%;
 }
 .main-banner-content h4{
   font-size: 14px;
   font-weight: 400;
   line-height: 24px;
   color: var(--color-bf4800);
   margin: 0 0 12px;
   letter-spacing: 0.3px;
   text-transform: uppercase;
 }
 .main-banner-content h5{
   font-size: 42px;
   line-height: 54px;
   font-weight: 500;letter-spacing: -2px;
   padding: 0;
   text-transform: none;
 }
 .main-banner-content p{
   font-size: 18px;
   line-height: 28px;
   letter-spacing: 0.4;
   color: var(--color-131921);
 }
 .button{
   background-color: var(--color-232f3e);
   color: white;
   font-size: 14px;
   padding: 13px 33px;
   border-radius: 25px;
 }
 .button:hover{
   background-color: var(--color-febd69);
   color: #131921;
 }
 .small-banner-content{
   top: 25%;
   left: 10%;
 }
 .small-banner-content h4{
   font-size: 14px;
   font-weight: 400;
   line-height: 24px;
   color: var(--color-bf4800);
   margin: 0 0 12px;
   letter-spacing: 0.3px;
   text-transform: uppercase;
 }
 .small-banner-content h5{
   font-size: 22px;
   line-height: 34px;
   font-weight: 500;letter-spacing: -2px;
   padding: 0;
   text-transform: none;
 }
 .small-banner-content p{
   font-size: 14px;
   line-height: 28px;
   letter-spacing: 0.4;
   color: var(--color-131921);
 }
 .small-banner{
   width: 49%;
   
 }